.App {    
  margin: auto;
  width: calc(100vw - 400px);     
  max-width: 1280px;
  min-width: 1024px;
}


body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 15px;
}

button {
  padding: 10px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color:#232f3e;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

h4 {
  font-size: 0.8rem;
}

nav {
  border-bottom: 4px solid orange;
  text-align: center;
  top: 0;
  width: 100%;
  background-color: #232f3e;
  font-size: 1.1em;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;  
  color: white;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {
  /* background: black; */
  /* background: white; */
  color: orangered;
}

#page-body {
  margin: auto;
  padding: 10px 150px;
  background-color: white;
  overflow-y: auto;
  height: calc(100vh - 75px);  
}

.products-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.products-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px dashed black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
  font-size: 1rem;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: auto;
  background-color: #232f3e;
}

#votes-section button, p {
  display: inline-block;  
}

#votes-section span.vote-count {
  size: 1.5em;
  width: 1.5em;
  height: 1.5em;
  display: inline-block;
  vertical-align: top;  
  padding: 0 10px;  
}

#votes-section h3 {
  margin-bottom: 16px;
  font-size: 0.8rem;
}


.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}